import { ClientesService } from './../../../core/consultaService/clientes.service';
import { CreaFoliosAutorizadosComponent } from './../../commonPopUp/creaFoliosAutorizadosPopUp/creaFoliosAutorizados.component';
import { Component, OnInit, OnDestroy, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, AbstractControl } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { NgxSpinnerService } from 'ngx-spinner';
import { UtilService } from './../../../services/util.service';
import { DocumentosService } from "./../../../core/consultaService/documentos.service";
import { AuthService } from '../../../core/consultaService/auth.service';
import { CreaSolicitudFolioPopUpComponent } from "./../../commonPopUp/creaSolicitudFolioPopUp/creaSolicitudFolioPopUp.component";
let swal = require('sweetalert');

@Component({
  selector: 'app-solicitudFolios-folio',
  templateUrl: './solicitudFolios.component.html',
  styleUrls: ['./solicitudFolios.component.scss']
})
export class SolicitudFoliosComponent implements OnInit {
  requestDos: any = {};
  request: any = {};
  fechaActual = new Date();
  forma: FormGroup;

  user: any = {};
  foliosDC08: Array<any> = [];
  tiposfacturas: Array<any> = [];
  bsModalRef: BsModalRef;
  pageActual = 1;
  date = new Date();

  constructor(
    public modalService: BsModalService,
    public loader: NgxSpinnerService,
    public utilService: UtilService,
    public documentosService: DocumentosService,
    public authService: AuthService,
    public clientesService: ClientesService

  ) {
    this.forma = new FormGroup({
      fechaDesde: new FormControl(this.fechaActual.getFullYear()  + this.fechaActual.getMonth() + this.fechaActual.getDate()),
      fechaHasta: new FormControl(''),
      cantidadHasta: new FormControl(''),
      cantidadDesde: new FormControl(''),
      tipoFactura: new FormControl('')
    });
  }

  ngOnInit() {

    this.forma.controls.fechaDesde.setValue(this.firstDayLastMonth(this.fechaActual));
    this.forma.controls.fechaHasta.setValue(this.utilService.dateToStringFormat(this.fechaActual));
    this.request.parcodparametro01 = "";
    this.request.parcodparametro02 = "";
    this.request.parcodparametro03 = "";
    this.request.parcodparametro04 = "";
    this.request.parcodparametro05 = "";
    this.request.parnumparametro = 21;

    this.documentosService.buscaParametros(this.request).subscribe((res) => {
      this.tiposfacturas = res['general']
      console.log("TCL: ConsultaFolioComponent -> ngOnInit ->  this.tiposfacturas ", this.tiposfacturas)
    })
  }

  firstDayLastMonth(date) {
    let anio = date.getFullYear();
    let mes = date.getMonth() ;
    let dia = 1;

    let diaStr = dia < 10 ? "0" + dia : dia;
    let mesStr = mes < 10 ? "0" + mes : mes;
    let fechaString = `${anio}-${mesStr}-${diaStr}`;
    return fechaString;
  }

  creaSolicitudFolio() {
    let initialState = {
      th1: "Número",
      th2: "Nombres",
      modalFor: "Locales",
      foliosDC08: this.foliosDC08
    };

    this.bsModalRef = this.modalService.show(CreaSolicitudFolioPopUpComponent, {
      initialState,
      backdrop: "static",
      keyboard: false
    });
  }

  buscaFolios() {
    this.requestDos.nnumcantidaddesde = Number(this.forma.controls.cantidadDesde.value);
    this.requestDos.nnumcantidadhasta = Number(this.forma.controls.cantidadHasta.value);
    this.requestDos.nrutoperador = Number(this.user.nrutoperador);
    this.requestDos.scodtipodocumento = this.forma.controls.tipoFactura.value;
    this.requestDos.sfechadesde = this.forma.controls.fechaDesde.value.split("-").join("") + "00000000";
    this.requestDos.sfechahasta = this.forma.controls.fechaHasta.value.split("-").join("") + "00000000";
    this.loader.show();
    this.documentosService.solicitudesFolios(this.requestDos).subscribe((data) => {
      this.loader.hide();
      if (!data) {
        swal('Atención', 'No se encontraton resultados!', 'error');
      }

      else{
        this.foliosDC08 = data['documentos']
      }
    })
  }

  closeModal(){
    this.bsModalRef.hide();
  }

}
