import { DocumentosService } from "./../../../core/consultaService/documentos.service";
import { ConsultasService } from "./../../../core/consultaService/consultas.service";
import { Component, OnInit } from "@angular/core";
import { NgxSpinnerService } from "ngx-spinner";
import { BsModalService, BsModalRef } from "ngx-bootstrap/modal";
import * as moment from 'moment';

import { DetalleImpresionFacturasErrorPopUpComponent } from "../../commonPopUp/detalleImpresionFacturasErrorPopUp/detalleImpresionFacturasErrorPopUp.component";

import {  FormGroup, FormBuilder, Validators, FormControl } from "@angular/forms";
import { CajaCobranzaService } from "../../../core/consultaService/cajaCobranza.service";
import { AuthService } from "../../../core/consultaService/auth.service";
import { UtilService } from "../../../services/util.service";
import { DecimalPipe } from "@angular/common";
import { DatePipe } from "@angular/common";
declare var jQuery: any;
declare var $: any;
const swal = require('sweetalert');

@Component({
  selector: "app-impresionfactura",
  templateUrl: "./impresionFactura.component.html",
  styleUrls: ["./impresionFactura.component.scss"]
})
export class ImpresionfacturaComponent implements OnInit {
  origen: string;
  param: string;
  titulo: string;
  forma: FormGroup;
  forma2: FormGroup;
  movimiento: any;
  cliente: any;
  user: any;
  cargando: boolean;
  estados: any;
  hidden = true;
  showResults: boolean;
  estadoLotesImpD: any;
  rutOperador: any;
  request: any = {};
  fechaDesde: any;
  currentdate: any;
  tipos: any;
  requestDos: any = {};
  lotesImp: any;
  scodsucursal: any;
  resultados: any;
  pActualBuscaImp = 1;
  bsModalRefe: BsModalRef;
  fechaHasta: any;
  fechaActual =  new Date();
  hasta:string;
  desde:string;

  constructor(
    private documentosService: DocumentosService,
    fb: FormBuilder,
    public loader: NgxSpinnerService,
    private authService: AuthService,
    private modalService: BsModalService,
    private consultasService: ConsultasService,
    private cajaCobranzaService: CajaCobranzaService,
    private utilService: UtilService
  ) {
    this.forma = new FormGroup({
      tipoFactura: new FormControl(),
      estadoFactura: new FormControl(),
      lote: new FormControl(),
      recorrido: new FormControl(),
      fechaDesde: new FormControl(),
      fechaHasta: new FormControl()
    });

    this.currentdate = new Date();
  }

  ngOnInit() {

    this.showResults = false;
    this.cargando = false;
    this.resultados = true;

    this.request.nrutoperador = this.rutOperador = parseInt(
      JSON.parse(localStorage.getItem("DatosUsuario"))["nrutoperador"],
      10
    );

    this.requestDos.scodsucursal = JSON.parse(
      localStorage.getItem("SucursalUsuario")
    )["sus_scodsucursal"];

    this.forma.controls.fechaDesde.setValue(
      this.currentdate.getFullYear().toString() +
        "-" +
        (this.currentdate.getMonth() + 1).toString().padStart(2, 0) +
        "-" +
        this.currentdate
          .getDate()
          .toString()
          .padStart(2, 0)
    );

    this.forma.controls.fechaHasta.setValue(
      this.currentdate.getFullYear().toString() +
        "-" +
        (this.currentdate.getMonth() + 1).toString().padStart(2, 0) +
        "-" +
        this.currentdate
          .getDate()
          .toString()
          .padStart(2, 0)
    );

    console.log(this.rutOperador);

    this.documentosService.getestadoLotesImpD(this.request).subscribe(res => {

      this.estados = res["documentos"];
      console.log('this.estados', this.estados)
    });

    this.documentosService.buscatiposImpresion(this.request).subscribe(res => {
      this.tipos = res["documentos"];
      console.log('this.tipos', this.tipos)
    });
  }

  setFecha(){
    /*  var currentDate = moment();
     var futureMonth = moment(currentDate).add(1, 'M');
     var futureMonthEnd = moment(futureMonth).endOf('month');
     if(currentDate.date() != futureMonth.date() && futureMonth.isSame(futureMonthEnd.format('YYYY-MM-DD'))) {
         futureMonth = futureMonth.add(1, 'd');
     }
     this.desde = currentDate.format('YYYY-MM-DD');
     this.hasta = futureMonth.format('YYYY-MM-DD');
     this.forma.controls.sFhoHasta.setValue(this.hasta);
     this.forma.controls.sFhoDesde.setValue(this.desde ); */
     var today = moment();
     today = today.subtract(1, 'months');
     var month = today.format('M');
     var day   = today.format('D');
     var year  = today.format('YYYY');
     var startDate = moment([year, month, '1']);
     var endDate = moment([year, month, '30']);

     //var prueba = moment(startDate).format('YYYY-MM-DD');
     this.desde = moment(startDate).format('YYYY-MM-DD');
     this.hasta = moment(endDate).format('YYYY-MM-DD');
     this.forma.controls.sFhoHasta.setValue(this.hasta);
     this.forma.controls.sFhoDesde.setValue(this.desde);
   }

  buscaLotesImpresion() {
    console.log('this.requestDos', this.requestDos)
    this.resultados = true;

    this.cargando = true;
    this.requestDos.ncodlote = this.forma.controls.lote.value;
    this.requestDos.ncodrecorrido = this.forma.controls.recorrido.value;
    this.requestDos.nrutoperador = this.request.nrutoperador;
    this.requestDos.scodestado = this.forma.controls.estadoFactura.value;
    this.requestDos.scodtipoimpresion = this.forma.controls.tipoFactura.value;
    this.requestDos.sfechadesde =
      this.forma.controls.fechaDesde.value.split("-").join("") + "00000000";
    this.requestDos.sfechahasta =
      this.forma.controls.fechaHasta.value.split("-").join("") + "00000000";
    this.requestDos.scodsucursal = this.requestDos.scodsucursal;

    this.documentosService.buscalotesImp(this.requestDos).subscribe(res => {
      console.log("res", res);
        this.cargando = false;
        this.lotesImp = res["documentos"];
        console.log('this.lotesImp.length', this.lotesImp.length)

        console.log("this.lotesImp", this.lotesImp);
        console.log('this.lotesImp despues',  this.lotesImp)
         if( this.lotesImp.length == 0){
          this.resultados = false;
         }
    });
  }

  openModalError(lott: any = {}) {
    console.log(lott);


    const initialState = {
      lote: lott.lot_nidlote,
      idRecorrido: lott.lfel_id_recorrido,
      tipoImpresion: lott.tipoimpresion,
      fechaEntrega: lott.lot_dfhoentrega,
      estadoloteimpresion: lott.estadoloteimpresion,
      cantDocs: lott.lot_cantidaddocumentos,
      cantDocsError: lott.lot_cantidaddocerror,
      request2anterior: this.requestDos

    };

    this.bsModalRefe = this.modalService.show(
      DetalleImpresionFacturasErrorPopUpComponent,
      { initialState, backdrop: 'static', class: 'modal-lg'}

    );
  }

  imprimir() {
    let printContents, popupWin;
    printContents = document.getElementById("print-section").innerHTML;
    popupWin = window.open("", "_blank", "top=0,left=0,height=100%,width=auto");
    popupWin.document.open();
    popupWin.document.write(`
      <html>
        <head>
          <title>Print tab</title>
          <style>
          </style>
        </head>
    <body onload="window.print();window.close()">${printContents}</body>
      </html>`);
    popupWin.document.close();
  }
}
